.header {
  height: 45px;
  display: flex;
  justify-content: center;
  background: linear-gradient(to bottom,rgba(12, 1, 1, 0.885)30%,rgba(12, 1, 1, 0.827)60%, rgba(12, 1, 1, 0)100%);
  /* position: absolute; */
}
.header-menu {
  padding: 10px;
  display: flex;
  justify-content: space-between;
  gap: 10px;
}
.header-menu_button {
  display: flex;
  text-decoration: none;  
  align-items: center;
  color: rgb(227, 217, 217);
}
.header-menu_button:hover {
  text-shadow: 0px 0px 10px #4434f79c; 
  transform: scale(1.1) ;
}
.header-menu_button__count {
  max-width: 30px;
  margin: 0 2px;
  
  white-space: nowrap; 
  overflow: hidden; 
  text-overflow: ellipsis; 
}
.login_button {
  padding: 0 3px 2px 2px;
  color: rgb(182, 147, 52);
  border: 1px solid rgb(182, 147, 52);
  border-radius: 3px;
}
@media (max-width: 360px) {
  .header-menu_button {
    font-size: 12px;
  }
}